// .............. input circle 1
.#{$checkbox}-square-1 {

    font-size: $base-font-size;

    input[type=checkbox] {
        position: absolute;
        opacity: 0;
    }

    label {
        position: relative;
        cursor: pointer;
        display: block;
        width: $circle-style-width;
        height: $circle-style-height;
        &:before {
            content: '';
            position: absolute;
            width: 1em;
            height: 1em;
            transition: background $base-easing-speed .1s $base-easeing;
            background: $base-color;
            box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.3);
            // border-radius: $border-radius-full;
        }
        &:after {
            content: '';
            position: absolute;
            width: .6em;
            height: .6em;
            // border-radius: $border-radius-full;
            left: .2em;
            top: .2em;
            transition: all $base-easing-speed*2 $base-easeing;
            background: $red;
            z-index: 2;
        }
    }

    input[type=checkbox]:checked+label {

        &:before {}
        &:after {
            background: $green;
        }
    }
}


// .............. input circle 2
.#{$checkbox}-square-2 {

    font-size: $base-font-size;

    input[type=checkbox] {
        position: absolute;
        opacity: 0;
    }

    label {
        position: relative;
        cursor: pointer;
        display: block;
        width: $circle-style-width;
        height: $circle-style-height;
        &:before {
            content: '';
            position: absolute;
            width: $circle-style-width;
            height: $circle-style-height;
            transition: background $base-easing-speed .1s $base-easeing;
            background: $base-color;
            box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.3);
            // border-radius: $border-radius-full;
        }
        &:after {
            content: '';
            position: absolute;
            width: .6em;
            height: .6em;
            // border-radius: $border-radius-full;
            left: .2em;
            top: .2em;
            transition: all $base-easing-speed*2 $base-easeing;
            background: $red;

            animation: switch-off-circle .2s ease-out;

        }
    }

    input[type=checkbox]:checked+label {

        &:before {}
        &:after {
            background: $green;
            width: 1em;
            height: 1em;
            top: 0;
            left: 0;
            animation: switch-on-circle .2s ease-out;

        }
    }
}

